body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Cantarell", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main, nav {
  margin: 0 1rem;
}

nav {
  padding: 0.6rem 0;
}

.row {
  display: flex;
  width: 100%;
}

.row>div {
  flex-grow: 1;
  margin: 0 2px;
  border-radius: 8px;
  border: 1px solid silver;
}

.control:nth-child(1) {
  width: 34%;
}

.control:nth-child(2) {
  width: 6%;
}

.control:nth-child(3) {
  width: 40%;
}

.control:nth-child(4) {
  width: 15%;
}

.idiom {
  margin: 2px;
  padding: 1rem;
}

.idiom:nth-child(1) {
  width: 40%;
}

.idiom:nth-child(2) {
  width: 60%;
}
/* ========== PLOT ========== */

.axis-x {
  fill: black;
  /* font-weight: bold; */
  text-anchor: end;
}

.axis-y {
  fill: black;
  /* font-weight: bold; */
  text-anchor: start;
}

.circles {
  fill: #08306b;
  stroke: #03132b;
  stroke-width: 0.8;
  opacity: 0.7;
}

.text {
  font-family: sans-serif;
  font-size: 10px;
}

/* ========== LINES ========== */

.line {
  fill: none;
  stroke-width: 2;
}

.dots {
  stroke-width: 2;
}

/* ========== MAP ========== */

.countries {
  fill: green;
}

.countries path {
  stroke: #aac;
  stroke-width: 0.6;
  opacity: 0.8;
}

.countries path:hover {
  stroke-width: 1.2;
  opacity: 1;
}

.names {
  fill: none;
  stroke: white;
  stroke-linejoin: round;
}

.names:hover {
  fill: black;
}

/* ========== CHORD ========== */
